.nav{
    position: fixed;
    top: 0;
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 20px;
    /* background-color: black; */
    height: 30px;
    z-index: 1;
    transition-timing-function: ease-in;
    transition: all 0.5s;
 
}
.nav_black {
    background-color: #111;
}
.nav__logo{
    position: fixed;
    left: 20px;
    object-fit: contain;
}

.nav__avatar{
      position: fixed;
      right: 20px;
    width: 30px;
    object-fit: contain;
}